	setPrize(obj: any, index: any) {
		console.log(obj, index)
		// this.form.prizes[index] = deepClone(obj)
		this.$set(this.form.prizes, index, deepClone(obj))
		this.$forceUpdate()
	}

	onSubmit() {
		if (this.mode == PageMode['ADD']) {
			this.luckyDrawAdd()
		} else if (this.mode == PageMode['EDIT']) {
			this.luckyDrawEdit()
		}
	}


	luckyDrawDetail() {
		apiLuckyDrawDetail({
				id: this.identity
			})
			.then((res: any) => {
				console.log('res', res)

				res.start_time = res.start_time_desc
				res.end_time = res.end_time_desc

				this.form = res
			})
	}

	luckyDrawEdit() {
		apiLuckyDrawEdit(this.form)
			.then((res: any) => {
				setTimeout(() => {
					this.$router.go(-1)
				}, 500)
			})
			.catch((err: any) => {
				console.log('err', err)
			})
	}

	luckyDrawAdd() {
		apiLuckyDrawAdd(this.form)
			.then((res: any) => {
				setTimeout(() => {
					this.$router.go(-1)
				}, 500)
			})
			.catch((err: any) => {
				console.log('err', err)
			})
	}

	created() {
		const query: any = this.$route.query

		if (query.mode) this.mode = query.mode

		// 编辑模式：初始化数据
		if (this.mode === PageMode['EDIT']) {
			this.identity = query.id * 1
			this.status = query.status
			this.type = query.type
			this.luckyDrawDetail()
		} else {
			this.form.prizes = this.lists
		}
	}
